import React, { Fragment } from 'react';
import { observer } from 'mobx-react';
import './style.css';
import { Link } from 'react-router-dom';
import { Form, FormControl, Nav, Collapse, Navbar, NavDropdown, Button, Row } from 'react-bootstrap';
import { Container } from 'semantic-ui-react';
import { withCookies } from 'react-cookie';

class Header extends React.Component {
    // Шапка сайта

    componentDidMount() {
        const userId = 1;
        this.props.store.checkSession();
        console.log('checked');
        // this.props.store.getProfile(userId);
    }

    render() {
      //  const { citizen, isLoading } = this.props.store;
        const { isAuthenticated } = this.props.store;

        return (
                    <Navbar className="app-header" expand="lg">
                        <Navbar.Brand className="mr-auto" href="#home"><img  src={require('../../../images/Hypp150.jpg')} ></img></Navbar.Brand>
                        <Navbar.Toggle><i class="fas fa-bars fx-2"></i></Navbar.Toggle>
                        <Navbar.Collapse id="basic-navbar-nav navbar-inverse">
                            <Nav className="mr-auto">
                            <Nav.Item>
                                <Link to="/">Главная</Link>
                            </Nav.Item>
                                <Nav.Item>
                                <Link to="/mce">ЖКХ</Link>
                                </Nav.Item>
                                <Nav.Item>
                                <Link to="/updates">Объявления</Link>
                                </Nav.Item>
                                <Nav.Item>
                                <Link to="/votes">Голосование</Link>
                                </Nav.Item>   
                                <Nav.Item>
                                <Link to="/contacts">Контакты</Link>
                                </Nav.Item>
                            </Nav>
                            { isAuthenticated === true ? 
                                <Nav>
                                    <Nav.Item>
                                        <Link to="/profile">Профиль</Link>
                                    </Nav.Item>
                                </Nav>
                                :
                                <Nav>
                                    <Nav.Item>
                                        <Link to="/login">Войти</Link>
                                    </Nav.Item>
                                    <Nav.Item>
                                        <Link to="/register">Регистрация</Link>
                                    </Nav.Item>
                                </Nav>
                            }
                        </Navbar.Collapse>
                    </Navbar>
        )
    }
}

Header = observer(Header);

export default withCookies(Header);